@import url(https://fonts.googleapis.com/css2?family=Mulish:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  font-family: 'Mulish', 'Arial', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  font-family: 'Mulish', sans-serif;
  color: #373737;
}

.navbar{width:100%;height:10vh}.navbar .nav-top{background-color:#fff}.navbar .nav{background-color:#fff;box-shadow:0px 4px 50px 10px rgba(168,168,168,.1)}.navbar .logo{font-weight:700;font-size:24px;margin-left:2rem;color:#e05d2a !important}.navbar .logo:hover{transform:translateY(2px)}.navbar .nav-link{font-weight:600;font-size:18px;margin:0rem 1.5rem;color:#373737 !important}.navbar .nav-link:hover{color:#e05d2a !important;transform:translateY(2px)}.navbar .contact{margin-right:3rem}.navbar .toggle{border:none !important;color:#373737 !important}@media screen and (max-width: 991px){.navbar .navbar-toggler{background:#fff !important;transition:.3s}.navbar .navbar-collapse{width:100%;overflow-x:hidden;background:#fff !important;border-radius:15px;padding:.5rem !important;box-shadow:0px 4px 50px 10px rgba(168,168,168,.1);transition:.3s !important}.navbar .logo{margin-left:1rem}.navbar .nav-link{font-size:16px}}
#cover{width:100%;min-height:90vh}#cover .content-container{padding:10% 15%;width:100%}#cover .info-container h1{font-weight:700;font-size:48px;margin-top:3rem}#cover .info-container h2{font-weight:700;font-size:36px;margin-top:1rem}#cover .info-container h3{font-weight:600;font-size:18px;width:20rem}#cover .info-container .contact-btn{font-size:14px;font-weight:700;background-color:#e05d2a;border:0;border-radius:15px;margin:2rem 0rem;padding:.8rem 1rem;width:8rem;transition:.5s}#cover .info-container .contact-btn:hover{background-color:#fff;box-shadow:0px 0px 0px 2.5px #fff,0px 0px 0px 5px #e05d2a,0px 0px 0px 10px #fff,0px 0px 0px 10.5px #e05d2a;color:#e05d2a;transition:.5s}#cover .info-container .contact-btn:focus{box-shadow:0px 0px 0px 2.5px #fff,0px 0px 0px 5px #e05d2a,0px 0px 0px 10px #fff,0px 0px 0px 10.5px #e05d2a}#cover .img-container{display:flex}#cover .img-container .img{height:auto;width:25rem;margin:auto}@media screen and (max-width: 600px){#cover .content-container{padding:5% 10%}#cover .info-container h1{font-size:42px}#cover .info-container h2{font-size:32px}#cover .info-container h3{font-size:16px}#cover .img-container .img{width:60vw;margin-top:1rem}}
#about{width:100%;min-height:90vh;display:flex;flex-direction:column}#about .content-container{padding:5% 10%;width:100%;margin:auto}#about .content-container .info-container h1{font-weight:700;font-size:24px;margin-top:3rem}#about .content-container .info-container p{font-size:16px;margin-top:1rem}#about .content-container .img-container{display:flex}#about .content-container .img-container .img{height:auto;width:18rem;margin:auto}#about .content-container .role-container{display:flex;flex-direction:column;align-items:center;padding:1rem}#about .content-container .role-container .role-img{height:auto;width:10rem;margin:auto}#about .content-container .role-container p{font-weight:600;font-size:18px;margin-top:2rem;margin-bottom:5rem;text-align:center}@media screen and (max-width: 600px){#about .content-container .info-container h1{font-size:20px}#about .content-container .info-container p{font-size:14px}#about .content-container .img-container .img{width:15rem}#about .content-container .role-container .role-img{width:8rem}#about .content-container .role-container p{font-size:16px}}
#experience{width:100%;min-height:90vh;display:flex;flex-direction:column}#experience .content-container{padding:5% 10%;width:100%;margin:auto}#experience .content-container h1{font-size:24px;font-weight:700;text-align:center;margin:3rem 0rem}#experience .content-container .jobs-container{padding-left:5rem}#experience .content-container .jobs-container .job{margin:2rem 0rem}#experience .content-container .jobs-container .logo-container{margin-right:2rem}#experience .content-container .jobs-container .logo{height:60px;width:auto;margin-top:1rem}#experience .content-container .jobs-container .logo:hover{transform:translateY(2px)}#experience .content-container .jobs-container h2{font-size:20px;font-weight:700}#experience .content-container .jobs-container h2:hover{transform:translateY(1px)}#experience .content-container .jobs-container a{color:#373737 !important}#experience .content-container .jobs-container a:hover{text-decoration:none;color:#e05d2a !important}#experience .content-container .jobs-container h3{font-size:14px;font-weight:500;color:#afafaf}#experience .content-container .jobs-container p{font-size:18px}#experience .content-container .toolbox-container{display:flex;flex-direction:column}#experience .content-container .toolbox-container .toolbox{margin:auto}#experience .content-container .toolbox-container .toolbox .icon-container .icon{height:50px;width:auto;margin:1.5rem}#experience .content-container .toolbox-container .toolbox .icon-container .tooltip{background-color:#fff !important}#experience .content-container .toolbox-container .resume-btn{font-weight:700;font-size:14px;background-color:#e05d2a;border:0;border-radius:15px;margin:auto;margin-top:2rem;padding:1rem;width:10rem;transition:.5s}#experience .content-container .toolbox-container .resume-btn:hover{background-color:#fff;box-shadow:0px 0px 0px 2.5px #fff,0px 0px 0px 5px #e05d2a,0px 0px 0px 10px #fff,0px 0px 0px 10.5px #e05d2a;color:#e05d2a;transition:.5s}#experience .content-container .toolbox-container .resume-btn:focus{box-shadow:0px 0px 0px 2.5px #fff,0px 0px 0px 5px #e05d2a,0px 0px 0px 10px #fff,0px 0px 0px 10.5px #e05d2a}@media screen and (max-width: 600px){#experience .content-container h1{font-size:20px}#experience .content-container .jobs-container{padding-left:1rem}#experience .content-container .jobs-container h2{font-size:18px}#experience .content-container .jobs-container h3{font-size:12px;margin-top:1rem}#experience .content-container .jobs-container p{font-size:16px}#experience .content-container .jobs-container .logo{height:50px}}
#projects{width:100%;min-height:90vh;display:flex;flex-direction:column}#projects h1{font-size:24px;font-weight:700;text-align:center;margin-top:3rem}#projects .project-container{margin:auto;display:flex;flex-direction:column}#projects .project-container .project-row{margin:auto;display:flex}#projects .project-container .project-row .project{margin:auto;display:flex;flex-direction:column}#projects .project-container .project-row .project .project-img{height:auto;width:500px;margin:auto}#projects .project-container .project-row .project .project-img:hover{transform:translateY(3px)}@media screen and (max-width: 600px){#projects h1{font-size:20px}#projects .project-container .project-row .project .project-img{width:80vw}}
#contact{width:100%;min-height:25vh;display:flex;flex-direction:column}#contact h1{font-size:24px;font-weight:700;text-align:center;margin-top:3rem}#contact h3{font-size:18px;text-align:center;margin:2rem 0rem}#contact .mail-icon{color:#e05d2a;width:3rem;margin-bottom:2px}#contact .mail-icon:hover{transform:translateY(2px)}#contact .socials{margin:auto}#contact .socials .icon{margin:2rem;height:50px;width:auto;color:#e05d2a}#contact .socials .icon:hover{transform:translateY(2px)}#contact .footer{width:100%;padding:1rem 3rem}#contact .footer p{font-size:14px;font-weight:400}@media screen and (max-width: 600px){#contact h1{font-size:20px}#contact h3{font-size:16px}#contact .socials .icon{margin:1rem;height:40px}#contact .footer p{font-size:12px;text-align:center;margin-top:1rem}}
